import AppBar from '@material-ui/core/AppBar'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import IconButton from '@material-ui/core/IconButton'
import Slide from '@material-ui/core/Slide'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import CloseIcon from '@material-ui/icons/Close'
import Table from '@material-ui/core/Table'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableBody from '@material-ui/core/TableBody'
import Switch from '@material-ui/core/Switch'
import PropTypes from 'prop-types'
import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'

import styles from '../../../../resources/theme/users'
import EnhancedComponent from '../../../components/EnhancedComponent'
import { fetchDistributionCentersPerSuppliers } from '../../../distributionCenters/DistributionCentersActions'
import { getEditingDistributionCenters } from '../../../distributionCenters/DistributionCentersReducer'

function Transition(props) {
  return <Slide direction="down" {...props} />
}

class List extends EnhancedComponent {
    state = {
      dirty: false,
        fields: {
         CD: []
        },
      }

      componentWillReceiveProps(nextProps) {
        if (!this.state.dirty && nextProps.data) {
          this.setState({
            fields: {
              CD: nextProps.data,
            },
          })
        }
      }

  // componentDidMount() {
  //   this.props.fetchDistributionCentersPerSuppliers(this.props.match.params.id).then(resp => {
  //     console.log(resp)
  //     this.setState({
  //       dirty: true,
  //       fields: {
  //         ...this.state.fields,
  //       CD: { ...this.props.items}
  //       },
  //     }, console.log(this.state))
  //   })
  // }

/*   onSubmit = values => {
    const { fetchSuppliers } = this.props
    // valida os campos se necessário
    this.props.update(values).then(data => {
      fetchSuppliers()
      if (data && data.code === 200) {
        this.props.history.push('/suppliers')
      }
    })
  }*/

  handleChange = name => event => {
    let localState = this.state.fields.CD
    localState[name].status = event.target.checked

    this.setState({
      fields: {
        ...this.state.fields,
        localState
      },
    })
  }

  onSubmit = e => {
    e.preventDefault()
    this.props.onSubmit && this.props.onSubmit(this.state.fields.CD)
  }

  onClose = () => {
    this.props.onClose && this.props.onClose();
  } 

  render() {
    const { classes, data } = this.props
    return (
      <Dialog
        open={data !== null}
        fullScreen
        onClose={this.onClose}
        TransitionComponent={Transition}
      >
        <form onSubmit={this.onSubmit}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <Typography variant="h6" color="inherit" className={classes.flex}>
              Editar Centros de Distribuição associados ao Fornecedor
            </Typography>
            <IconButton
              color="inherit"
              onClick={this.onClose}
              aria-label="Close"
            >
              <CloseIcon />
            </IconButton>
          </Toolbar>
        </AppBar>
        <Table>
          <TableHead>
            <TableRow>
              <TableCell>Ativo/Inativo</TableCell> 
              <TableCell>Identificador</TableCell>
              <TableCell>Descrição</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {this.state.fields.CD.map((item, index) => (
              <TableRow key={item.id}>
								<TableCell><Switch
                        checked={this.state.fields.CD[index].status}
                        onChange={this.handleChange(index)}
                        value={item.id}
                        color="primary"
                      /></TableCell>
                <TableCell>{item.id}</TableCell>
                <TableCell>{item.description}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        <Button
                type="submit"
                variant="contained"
                color="primary"
                className={classes.submit}
              >
                Enviar
              </Button>
        </form>
      </Dialog>
    )
  }
}

List.propTypes = {
  classes: PropTypes.object.isRequired,
  
  //roles: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  //roles: state.user.roles || [],
  //...getEditingDistributionCenters(state)
    //items: state.distributioncenter.items_supplier
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      fetchDistributionCentersPerSuppliers,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(List))

