import { combineReducers } from 'redux'

import auth from '../auth/AuthReducer'
import store from '../stores/StoreReducer'
import user from '../users/UserReducer'
import main from './MainReducer'
import supplier from '../suppliers/SuppliersReducer'
import distributioncenter from '../distributionCenters/DistributionCentersReducer'

const rootReducer = combineReducers({
  main,
  auth,
  user,
  store,
  supplier,
	distributioncenter
})

export default rootReducer
